	module.exports = {"data":{"markdownRemark":{"html":"<p>作业本涨价了、纸杯涨价了、外卖餐盒涨价了，就连塑料袋也加入了涨价行列。济源晨报记者走访多家纸制品批发商、超市了解到，纸制品已全面涨价，而废纸收购价格也翻了一番。   </p>\n<h2>行业现象</h2>\n<h3>一箱A4纸涨价20到30元不等</h3>\n<p>王先生在凯旋城一家印刷公司工作。他介绍，从今年春节开始，纸价就一路飙升，不管是哪种类型的纸都在涨价。   </p>\n<p>“我们公司以往都从郑州经销商那里采购纸，这些纸主要在山东、江苏等地生产。从今年开始，纸价不断攀升，印刷后的成品价格却没有上调，导致公司利润逐渐下降。”王先生说，和往年相比，今年A4纸涨价最为明显，一箱4000张的A4纸价格提高了20到30元不等。   </p>\n<p>南街集贸市场一家文具店的老板李女士说：“以前，一沓(10本)小学生田字格作业本的批发价是3~5元。今年，批发价涨了1元，所以售价也就上调了。对此，顾客有些不满，而我只能一遍遍地解释。”   </p>\n<p>市民张女士说：“这段时间以来，她明显感觉到了写字本价格的变化，虽然只涨了几块钱，但积少成多，最后也会多花不少钱。”   </p>\n<p>张女士家孩子今年上六年级，正处于小升初的重要阶段。张女士每隔两周就会到南街集贸市场给孩子购买作业本。   </p>\n<p>济源晨报记者调查发现，与纸有关的物品均涨价了。回收废纸箱的刘师傅告诉济源晨报记者，现在回收废纸箱、旧报纸的价格也水涨船高。目前，品相稍好的纸箱回收价格已经达到0.9元/斤，稍差一点的价格为0.7元/斤至0.8元/斤，旧报纸的价格是0.7元/斤左右。   </p>\n<h2>涨价原因</h2>\n<h3>市场发生变化，源头“缩水”，供不应求</h3>\n<p>“就拿普通的A4纸来说，我们公司上周刚进了500箱，但这也就是一两个月的用量。合作多年的经销商透露，纸价还要继续上涨。要不是资金压力较大，我们公司肯定会囤大量A4。”从事广告彩印工作的范经理说，“我们公司耗材比较多，像最基本的A4纸，每月用量在数百箱。以往，如果价格波动不大，经销商一般不会涨价。而从今年开始，形势就不一样了。经销商顶不住成本上涨压力，不断涨价。因此，我们公司的印刷成品价格也在上涨。”   </p>\n<p>那么，纸价上涨为何会上涨?   </p>\n<p>有业内人士介绍，此次纸价上涨主要还是因为全国生产纸的厂家减少，且生产规模缩水。此外，物流运输等行业的兴起，使得包装纸等产品的需求进一步加大，纸张供不应求。</p>\n<h2>如何应对</h2>\n<h3>从节约每一张纸开始</h3>\n<p>我市一家商贸公司的采购员张女士介绍，公司领导开会要求大家降低打印频率，节约用纸。如果必须要打印文件的话，原来的单页打印改为双面打印。此外，打字时设置的页边距小一点，行距小一点，字体小一号。\n“就连合同，也要求双面打印，一方面为控制成本，另一方面倡导大家节约用纸、保护环境从身边小事做起。”张女士表示。   </p>\n<p>在我市某科技公司上班的李经理介绍：“以前，公司开会时都会打印很多份相同的策划书，给参会人员发放。现在，公司直接把策划书的内容呈现在会议室的电子屏上让大家看。公司提倡无纸化管理，绿色生活从节约每一张纸开始。”   </p>\n<p>市民李先生说：“我比较喜欢看书，也经常从网上买书。最近，我明显感觉店家送的优惠券少了，且部分书店现在也不提供包邮服务了。书价虽然还未明显提高，但网购一本书的综合费用比之前高了一些。所以，我现在经常往实体书店跑，在那里看书学习，遇到实在喜欢的书才会买下来。同时，我还会到市图书馆去借书看，这样更省钱，生活依然因为读书而精彩!”(见习记者 黄一冰 实习生 王峰)</p>","frontmatter":{"date":"2017/09/25","path":"/news/a3z","title":"A3纸尺寸,A3纸大小,A3纸的尺寸"}}},"pathContext":{}}

